html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #EBE7DE;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1, h2, h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  align-items: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  margin-top: 4rem;
}

h3 {
  font-size: 3.2rem;
}

.formulario {
  /*Estilos para la validación*/
}
@media (min-width: 768px) {
  .formulario {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.formulario__label {
  display: block;
  font-weight: 700;
  font-size: 2.4rem;
  padding: 1rem;
  cursor: pointer;
}
.formulario__grupo-input {
  position: relative;
}
.formulario__input {
  width: 100%;
  background-color: #FFFFFF;
  border: 3px solid transparent;
  border-radius: 3px;
  height: 4.5rem;
  line-height: 4.5rem;
  padding: 0 4rem 0 1rem;
  transition: 0.3s ease all;
}
.formulario__input:focus {
  border: 3px solid #0075FF;
  outline: none;
  box-shadow: 3px 0px 30px rgba(163, 163, 163, 0.4);
}
.formulario__input-error {
  font-size: 1.2rem;
  margin-bottom: 0;
  display: none;
}
.formulario__input-error-activo {
  color: red;
  display: block;
}
.formulario__validacion-estado {
  position: absolute;
  right: 1rem;
  bottom: 1.5rem;
  z-index: 1;
  font-size: 1.6rem;
  opacity: 0;
}
.formulario__checkbox {
  margin-right: 1rem;
  width: 2rem;
  height: 2rem;
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .formulario__checkbox {
    margin-top: 0;
  }
}
.formulario__grupo-terminos, .formulario__mensaje, .formulario__grupo-btn-enviar {
  grid-column: span 1;
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .formulario__grupo-terminos, .formulario__mensaje, .formulario__grupo-btn-enviar {
    grid-column: span 2;
    margin-top: 0;
  }
}
.formulario__mensaje {
  height: 4.5rem;
  line-height: 4.5rem;
  background-color: #F66868;
  padding: 0 1.5rem;
  border-radius: 3px;
  display: none;
}
.formulario__mensaje-activo {
  display: block;
}
.formulario__mensaje p {
  margin: 0;
}
.formulario__grupo-btn-enviar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.formulario__btn {
  height: 4.5rem;
  line-height: 4.5rem;
  background-color: #000000;
  color: #FFFFFF;
  font-weight: bold;
  cursor: pointer;
  border: none;
  transition: 0.1s;
  transition-property: box-shadow;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario__btn {
    width: 30%;
  }
}
.formulario__btn:hover {
  box-shadow: 3px 0px 30px #a3a3a3;
}
.formulario__mensaje-exito {
  color: green;
  display: none;
}
.formulario__mensaje-exito-activo {
  display: block;
}
.formulario__grupo-correcto .formulario__validacion-estado {
  color: #1ED12D;
  opacity: 1;
}
.formulario__grupo-incorrecto .formulario__label {
  color: red;
}
.formulario__grupo-incorrecto .formulario__validacion-estado {
  color: red;
  opacity: 1;
}
.formulario__grupo-incorrecto .formulario__input {
  border: 3px solid red;
}