@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario{
    @include m.tablet { 
        @include m.grid(2, 3rem);   
    }

    &__label{
        display: block;
        font-weight: 700;
        font-size: 2.4rem;
        padding: 1rem;
        cursor: pointer;
    }

    &__grupo-input{
        position: relative;//Para que el grupo tengo uno relativo

    }

    &__input{
        width: 100%;
        background-color: v.$blanco;
        border: 3px solid transparent;
        border-radius: 3px;
        height: 4.5rem;
        line-height: 4.5rem;
        padding: 0 4rem 0 1rem;
        transition: .3s ease all;


        &:focus{
            border: 3px solid v.$azul;
            outline: none; //Elimina el subrayado que le agrega el navegador
            box-shadow: 3px 0px 30px rgba(163, 163, 163, 0.4);
        }

        &-error{
            font-size: 1.2rem;
            margin-bottom: 0;
            display: none;
        }

        &-error-activo{//Modificador para activar en la validacion
           color: red;
           display: block;
        }

    }//input

    &__validacion-estado{
        position: absolute;
        right: 1rem;
        bottom: 1.5rem;
        z-index: 1;
        font-size: 1.6rem;
        opacity: 0; //Para que no se muestre y se vea nada mas con un error
    }

    &__checkbox{    
        margin-right: 1rem;
        width: 2rem;
        height:2rem;
        margin-top: 4rem;
        @include m.tablet { 
            margin-top: 0;
        }
    }

    &__grupo-terminos,
    &__mensaje,
    &__grupo-btn-enviar{
        grid-column: span 1; //De esta forma abarca dos columnas
        margin-top: 4rem;

        @include m.tablet { 
            grid-column: span 2; //De esta forma abarca dos columnas
            margin-top: 0;
        }
    }

    &__mensaje{
        height: 4.5rem;
        line-height: 4.5rem;
        background-color: v.$error;
        padding: 0 1.5rem;
        border-radius: 3px;
        display: none;
    }

    &__mensaje-activo{
        display: block;
    }

    &__mensaje p{
      margin: 0;
    }

    &__grupo-btn-enviar{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__btn{
        height: 4.5rem;
        line-height: 4.5rem;
        background-color: v.$negro;
        color: v.$blanco;
        font-weight: bold;
        cursor: pointer;
        border: none;
        transition: .1s;
        transition-property: box-shadow;
        width: 100%;
        @include m.tablet { 
            width: 30%;
        }

        
        &:hover{
            box-shadow: 3px 0px 30px rgba(163, 163, 163, 1);
        }
    }

    &__mensaje-exito{
        color: green;
        display: none;
    }
    &__mensaje-exito-activo{
        display: block;
    }

/*Estilos para la validación*/
    &__grupo-correcto 
    &__validacion-estado{
       color: #1ED12D;
       opacity: 1; 
    }

    &__grupo-incorrecto 
    &__label{
       color: red;
    }

    &__grupo-incorrecto 
    &__validacion-estado{
       color: red;
       opacity: 1; 
    }
    &__grupo-incorrecto 
    &__input{
      border: 3px solid red; 
    }

}//Formulario